@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto);
@media only screen
and (min-width: 900px) {
  div#root {
    display: none;
  }
  
  div#desktop-page {
    display: block!important;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.back-button__icon {
  fill: #FFF;
}

.page__background {
  background-color: #F9F9F9;
}

.toolbar--material__left:empty,
.toolbar--material__right:empty {
  min-width: 15%;
}
.ErrorMsg {
  color: #FDCC30;
  font-weight: 500;
}
.NavBar {
  background-color: #42BDFF;
}

.NavBar-left {
  color: #FFF;
}

.NavBar-center {
  width: 70%;
  /* font-family: 'Roboto', sans-serif; */
  font-size: 20px;
  color: #FFFFFF;
  text-align: center;
}

.SignOut-icon {
  color: #FFFFFF;
  font-size: 20px;
}

.QuestionTask-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.QuestionTask-completed {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: #21CC41;
}

.QuestionTask-wrong {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: rgb(233, 75, 83);
}

.QuestionTask-description_wrapper {
  flex-basis: 30%;
  margin-bottom: 2rem;
  text-align: center;
}

.QuestionTask-activity_name {
  display: block;
  background: #EFEFEF;
  border-bottom: 1px solid #CCCCCC;
  padding: 1rem 0.6rem;
  font-size: 1.6rem;
}

.QuestionTask-activity_description {
  font-size: 20px;
  padding-top: 30px;
}

.QuestionTask-button {
  width: inherit;
  margin: 0.3rem 0.8rem;
  padding: 0.5rem 1.2rem;
  box-shadow: none;
  font-size: 1.2rem;
  font-weight: 500;
  text-align: left;
  text-transform: none;
}

.QuestionTask-button:disabled {
  background-color: #202020;
}
.PhotoTask-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.PhotoTask-completed {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  color: #FFFFFF;
  background-color: #21CC41;
  font-size: 28px;
  font-weight: 300;
}

.PhotoTask-description_wrapper {
  /* flex-basis: 50%; */
  margin: 2rem 0;
  text-align: center;
}

.PhotoTask-activity_name {
  display: inline-block;
  padding: 0.4rem 0.6rem;
  font-size: 2rem;
}

.PhotoTask-activity_description {
  width: 90%;
  margin: 2rem auto;
  font-size: 20px;
}

.PhotoTask-camera_wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 30%;
  background-color: #EEEEEE;
  text-align: center;
}

@-webkit-keyframes pulse {
  from { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(0.80); transform: scale(0.80); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes pulse {
  from { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(0.80); transform: scale(0.80); }
  to { -webkit-transform: scale(1); transform: scale(1); }
}

.PhotoTask-camera_label {
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.ShortAnswerTask-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.ShortAnswerTask-completed {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: #21CC41;
}

.ShortAnswerTask-wrong {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: rgb(233, 75, 83);
}

.ShortAnswerTask-description_wrapper {
  flex-basis: 30%;
  margin-bottom: 2rem;
  text-align: center;
}

.ShortAnswerTask-activity_name {
  display: block;
  background: #EFEFEF;
  border-bottom: 1px solid #CCCCCC;
  padding: 1rem 0.6rem;
  font-size: 1.6rem;
}

.ShortAnswerTask-activity_description {
  font-size: 20px;
  padding-top: 30px;
}

.ShortAnswerTask-answer_wrapper {
  text-align: center;
}

.ShortAnswerTask-input {
  width: 260px;
  margin-bottom: 3rem;
}

.ShortAnswerTask-input input {
  width: 260px;
  font-size: 1.2rem;
  padding: 1.2rem 0;
  text-align: center;
}

.ShortAnswerTask-input span {
  font-size: 1.2rem;
  color: #4F616B;
}

.ShortAnswerTask-button {
  width: 140px;
  padding: 0.3rem;
  font-size: 1rem;
  text-align: center;
}

.ActivityListPage-thumbnail {
  height: auto;
}

.ActivityListPage-task_title {
  font-size: 18px;
}

.ActivityListPage-checkmark {
  margin-bottom: -35%;
  margin-left: 25%;
  border-radius: 45%;
  background-color: #FFF;
  color: #008000;
}
.LocationPage-img-wrapper {
  text-align: center;
}

.LocationPage-img-wrapper img {
  width: 100%;
}

.LocationPage-title {
  margin-top: 15px;
  margin-bottom: 30px;
  text-align: center;
  /* font-family: 'Roboto', sans-serif; */
  font-weight: 500;
  font-size: 20px;
  color: #777777;
  text-transform: uppercase;
}

.LocationPage-info_wrapper {
  padding: 0 6%;
  margin-bottom: 2rem;
  /* font-size: 17px; */
  /* font-family: 'Open Sans', sans-serif; */
  color: #777777;
  line-height: 1.2;
  text-align: left;
}

.LocationPage-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.LocationPage-address_lbl {
  margin-bottom: 0;
  color: #42bdff;
  /* font-family: 'Roboto', sans-serif; */
  font-weight: 500;
}

.LocationPage-address {
  margin-top: 2px;
  /* font-size: 14px; */
  /* font-family: 'Roboto', sans-serif; */
}

.LocationPage-hours_wrapper {
  text-align: right;
}

.LocationPage-hours_lbl {
  margin-bottom: 0;
  color: #42bdff;
  /* font-family: 'Roboto', sans-serif; */
  font-weight: 500;
}

.LocationPage-hours {
  margin-top: 2px;
  /* font-size: 14px; */
  /* font-family: 'Roboto', sans-serif; */
}

.LocationPage-description_lbl {
  margin-bottom: 0;
  margin-top: 0;
  color: #42bdff;
  /* font-family: 'Roboto', sans-serif; */
  font-weight: 500;
}

.LocationPage-QR_description {
  text-align: center;
  padding: 0 1rem;
  font-size: 28px;
  font-weight: 300;
  color: #1f1f21;
}

.LocationPage-QR_cancel_btn {
  margin-top: 1rem;
  background-color: #ED1D24;
}
.LocationsPage-thumbnail {
  border-radius: 20%;
}

.LocationsPage-checkmark {
  margin-bottom: -50%;
  margin-left: -25%;
  border-radius: 45%;
  background-color: #FFF;
  color: #008000;
}
.SignUpPage-register_btn {
  margin: 0.3rem 0;
  font-size: 18px;
  background-color: #0CB34C;
}
.LoginPage {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  background-color: #42BDFF;
}

.LoginPage-title {
  text-align: center;
  font-size: 2rem;
  color: #FFFFFF;
}

.LoginPage-form {
  flex-basis: 50%;
  width: 80%;
}

.LoginPage-input {
  width: 95%;
  margin: 0.3rem 0;
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
  background-color: #FFFFFF;
}

.LoginPage-input input {
  font-size: 18px;
}

.LoginPage-login_btn {
  margin: 0.3rem 0;
  font-size: 18px;
  background-color: #E91E63;
}

.LoginPage-signup_btn {
  margin-top: 0.5rem;
  font-size: 18px;
  text-transform: none;
  color: #FFFFFF;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

