.LoginPage {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  background-color: #42BDFF;
}

.LoginPage-title {
  text-align: center;
  font-size: 2rem;
  color: #FFFFFF;
}

.LoginPage-form {
  flex-basis: 50%;
  width: 80%;
}

.LoginPage-input {
  width: 95%;
  margin: 0.3rem 0;
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
  background-color: #FFFFFF;
}

.LoginPage-input input {
  font-size: 18px;
}

.LoginPage-login_btn {
  margin: 0.3rem 0;
  font-size: 18px;
  background-color: #E91E63;
}

.LoginPage-signup_btn {
  margin-top: 0.5rem;
  font-size: 18px;
  text-transform: none;
  color: #FFFFFF;
}