.ShortAnswerTask-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.ShortAnswerTask-completed {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: #21CC41;
}

.ShortAnswerTask-wrong {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: rgb(233, 75, 83);
}

.ShortAnswerTask-description_wrapper {
  flex-basis: 30%;
  margin-bottom: 2rem;
  text-align: center;
}

.ShortAnswerTask-activity_name {
  display: block;
  background: #EFEFEF;
  border-bottom: 1px solid #CCCCCC;
  padding: 1rem 0.6rem;
  font-size: 1.6rem;
}

.ShortAnswerTask-activity_description {
  font-size: 20px;
  padding-top: 30px;
}

.ShortAnswerTask-answer_wrapper {
  text-align: center;
}

.ShortAnswerTask-input {
  width: 260px;
  margin-bottom: 3rem;
}

.ShortAnswerTask-input input {
  width: 260px;
  font-size: 1.2rem;
  padding: 1.2rem 0;
  text-align: center;
}

.ShortAnswerTask-input span {
  font-size: 1.2rem;
  color: #4F616B;
}

.ShortAnswerTask-button {
  width: 140px;
  padding: 0.3rem;
  font-size: 1rem;
  text-align: center;
}