.PhotoTask-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.PhotoTask-completed {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  color: #FFFFFF;
  background-color: #21CC41;
  font-size: 28px;
  font-weight: 300;
}

.PhotoTask-description_wrapper {
  /* flex-basis: 50%; */
  margin: 2rem 0;
  text-align: center;
}

.PhotoTask-activity_name {
  display: inline-block;
  padding: 0.4rem 0.6rem;
  font-size: 2rem;
}

.PhotoTask-activity_description {
  width: 90%;
  margin: 2rem auto;
  font-size: 20px;
}

.PhotoTask-camera_wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 30%;
  background-color: #EEEEEE;
  text-align: center;
}

@keyframes pulse {
  from { transform: scale(1); }
  50% { transform: scale(0.80); }
  to { transform: scale(1); }
}

.PhotoTask-camera_label {
  animation-name: pulse;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
}