.QuestionTask-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.QuestionTask-completed {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: #21CC41;
}

.QuestionTask-wrong {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
  font-size: 28px;
  font-weight: 300;
  color: #FFF;
  background-color: rgb(233, 75, 83);
}

.QuestionTask-description_wrapper {
  flex-basis: 30%;
  margin-bottom: 2rem;
  text-align: center;
}

.QuestionTask-activity_name {
  display: block;
  background: #EFEFEF;
  border-bottom: 1px solid #CCCCCC;
  padding: 1rem 0.6rem;
  font-size: 1.6rem;
}

.QuestionTask-activity_description {
  font-size: 20px;
  padding-top: 30px;
}

.QuestionTask-button {
  width: inherit;
  margin: 0.3rem 0.8rem;
  padding: 0.5rem 1.2rem;
  box-shadow: none;
  font-size: 1.2rem;
  font-weight: 500;
  text-align: left;
  text-transform: none;
}

.QuestionTask-button:disabled {
  background-color: #202020;
}